
/**
 * Code Scan CLI Entry Point
 *
 * Main entry point for the code-scan CLI tool that analyzes
 * git PR diffs for LLM-related security vulnerabilities.
 */

import { Command } from 'commander';
import * as packageJson from '../package.json';
import { executeScan } from './commands/scan';

const program = new Command();

program
  .name('code-scan')
  .description('Scan git PR diffs for LLM security vulnerabilities')
  .version(packageJson.version);

// Register scan command
program
  .command('scan')
  .description('Scan a git repository for LLM security vulnerabilities')
  .argument('<repo-path>', 'Path to the git repository')
  .option('-c, --config <path>', 'Path to configuration file')
  .option('-s, --server-url <url>', 'Scan server URL (default: https://api.promptfoo.dev)')
  .option('-k, --api-key <key>', 'Promptfoo API key for authentication (overrides config file)')
  .option('-b, --base <ref>', 'Base branch or commit to compare against (default: auto-detect main/master)')
  .option('--compare <ref>', 'Compare branch or commit (default: HEAD)')
  .option('--json', 'Output scan results as JSON with XML-formatted comments (for GitHub Actions)')
  .option('--github-pr <owner/repo#number>', 'GitHub PR to post comments to (e.g., promptfoo/promptfoo#123)')
  .action(executeScan);

program.parse();
