/**
 * Scan Client
 *
 * HTTP client for communicating with the scan server.
 */

import { request as undiciRequest, Agent } from 'undici';
import type { ScanRequest, ScanResponse } from '../types';
import { resolveAuthCredentials } from '../auth';

export class ScanClientError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'ScanClientError';
  }
}

// Create custom agent with finite generous timeouts
// These timeouts handle long-running scans without masking real hangs
const httpAgent = new Agent({
  connect: { timeout: 10_000 },      // 10s to establish connection
  headersTimeout: 30_000,            // 30s to receive response headers (server flushes early)
  bodyTimeout: 45 * 60_000,          // 45 minutes for full response body
  keepAliveTimeout: 75_000,          // 75s keep-alive
});

/**
 * Send a scan request to the server
 * @param request Scan request with diff, metadata, and config
 * @param serverUrl Base URL of the scan server (default: https://api.promptfoo.dev)
 * @param apiKey Optional API key for authentication (CLI arg or config file)
 * @returns Scan response with markdown report
 */
export async function sendScanRequest(
  request: ScanRequest,
  serverUrl: string = 'https://api.promptfoo.dev',
  apiKey?: string,
): Promise<ScanResponse> {
  const requestStartTime = Date.now();

  try {
    const url = `${serverUrl}/api/v1/code-scans`;

    console.error(`   Sending request to ${url}...`);

    // Build headers
    const requestHeaders: Record<string, string> = {
      'content-type': 'application/json',
    };

    // Resolve authentication credentials
    const auth = resolveAuthCredentials(apiKey);

    if (auth.apiKey) {
      requestHeaders['authorization'] = `Bearer ${auth.apiKey}`;
    } else if (auth.oidcToken) {
      requestHeaders['x-github-oidc-token'] = auth.oidcToken;
    } else {
      // No authentication - fail fast
      // TODO: Create docs at https://promptfoo.dev/docs/integrations/github-action
      // explaining how to set up code-scan as a GitHub Action with OIDC auth
      throw new ScanClientError(
        'Authentication required. Please authenticate using one of:\n' +
        '   - Run: promptfoo auth login\n' +
        '   - Pass: --api-key sk-your-token\n' +
        '   - Set apiKey in config YAML file\n' +
        '   - Install as GitHub Action: https://promptfoo.dev/docs/integrations/github-action'
      );
    }

    const authMethodName = auth.apiKey
      ? `API key (${auth.method})`
      : 'GitHub OIDC token';
    console.error(`    Authenticated via: ${authMethodName}`);

    // Use undici request() for better timeout control
    const { statusCode, headers, body } = await undiciRequest(url, {
      method: 'POST',
      headers: requestHeaders,
      body: JSON.stringify(request),
      dispatcher: httpAgent, // Use custom agent with generous timeouts
    });

    const responseReceivedTime = Date.now();
    console.error(`    Response received after ${(responseReceivedTime - requestStartTime) / 1000}s`);

    if (statusCode >= 400) {
      console.error(`     Server returned error status: ${statusCode}`);
      const errorText = await body.text();
      let errorMessage: string;

      try {
        const errorJson = JSON.parse(errorText);
        errorMessage = errorJson.error || errorJson.message || errorText;
      } catch {
        errorMessage = errorText;
      }

      throw new ScanClientError(
        `Server returned ${statusCode}: ${errorMessage}`,
      );
    }

    console.error(`    Parsing response JSON...`);
    const result = (await body.json()) as ScanResponse;
    console.error(`    Response parsed successfully`);

    if (!result.success) {
      throw new ScanClientError(result.error || 'Scan failed on server');
    }

    return result;
  } catch (error) {
    if (error instanceof ScanClientError) {
      throw error;
    }

    // Network errors, connection refused, etc.
    if (error instanceof Error) {
      // Log detailed error information for debugging
      console.error('\n Detailed error information:');
      console.error(`   Error name: ${error.name}`);
      console.error(`   Error message: ${error.message}`);
      console.error(`   Error stack: ${error.stack}`);

      // Check for common error codes
      const errorWithCode = error as Error & { code?: string; cause?: unknown };
      if (errorWithCode.code) {
        console.error(`   Error code: ${errorWithCode.code}`);
      }
      if (errorWithCode.cause) {
        console.error(`   Error cause: ${JSON.stringify(errorWithCode.cause, null, 2)}`);
      }

      if (error.message.includes('ECONNREFUSED')) {
        throw new ScanClientError(
          `Could not connect to scan server at ${serverUrl}. Is it running?`,
        );
      }

      // Handle specific undici timeout errors
      if (errorWithCode.code === 'UND_ERR_HEADERS_TIMEOUT') {
        const elapsed = Date.now() - requestStartTime;
        throw new ScanClientError(
          `Server did not send response headers within 30s after ${(elapsed / 1000).toFixed(1)}s. ` +
          `This indicates the server may not be flushing headers early. ` +
          `Check server configuration.`
        );
      }

      if (errorWithCode.code === 'UND_ERR_BODY_TIMEOUT') {
        const elapsed = Date.now() - requestStartTime;
        throw new ScanClientError(
          `Response body timeout after ${(elapsed / 1000).toFixed(1)}s. ` +
          `The scan took longer than the 45-minute limit. ` +
          `Consider breaking the PR into smaller batches.`
        );
      }

      if (errorWithCode.code === 'UND_ERR_CONNECT_TIMEOUT') {
        throw new ScanClientError(
          `Could not connect to scan server at ${serverUrl} within 10s. ` +
          `Check that the server is running and reachable.`
        );
      }

      // Provide more context about what failed
      if (error.message.includes('fetch failed') || error.message.includes('request failed')) {
        const elapsed = Date.now() - requestStartTime;
        throw new ScanClientError(
          `Network request failed after ${(elapsed / 1000).toFixed(1)}s. This could indicate:\n` +
          `   - Server closed connection prematurely\n` +
          `   - Network connectivity issue\n` +
          `   - Server error during processing\n` +
          `   See detailed error above. Original error: ${error.message}`
        );
      }

      // Handle ECONNRESET (connection reset by peer)
      if (error.message.includes('ECONNRESET')) {
        const elapsed = Date.now() - requestStartTime;
        throw new ScanClientError(
          `Connection was reset by server after ${(elapsed / 1000).toFixed(1)}s. ` +
          `This usually means the server closed the connection unexpectedly.`
        );
      }

      throw new ScanClientError(`Failed to send scan request: ${error.message}`);
    }

    throw new ScanClientError(`Failed to send scan request: ${String(error)}`);
  }
}
