import { z } from 'zod';

// ============================================================================
// Enums
// ============================================================================

export enum SeverityLevel {
  LOW = 'low',
  MEDIUM = 'medium',
  HIGH = 'high',
  CRITICAL = 'critical',
}

export enum DeploymentType {
  CLOUD = 'cloud',
  ONPREM = 'onprem',
}

export enum InstallationStatus {
  UNCONFIRMED = 'unconfirmed',
  ACTIVE = 'active',
  SUSPENDED = 'suspended',
}

export enum CodeScanStatus {
  PENDING = 'pending',
  RUNNING = 'running',
  COMPLETED = 'completed',
  FAILED = 'failed',
}

export enum TriggeredBy {
  GITHUB_ACTION = 'github_action',
  CLI = 'cli',
  API = 'api',
  UI = 'ui',
}

export enum SetupType {
  AUTO = 'auto',
  MANUAL = 'manual',
}

// ============================================================================
// Scan Request/Response Schemas (API endpoint payload)
// ============================================================================

export const FileRecordSchema = z.object({
  path: z.string(),
  status: z.string(),
  shaA: z.string().nullable(),
  shaB: z.string().nullable(),
  linesAdded: z.number().optional(),
  linesRemoved: z.number().optional(),
  beforeSizeBytes: z.number().optional(),
  afterSizeBytes: z.number().optional(),
  isText: z.boolean().optional(),
  skipReason: z.string().optional(),
  patch: z.string().optional(),
});

export const GitMetadataSchema = z.object({
  branch: z.string(),
  baseBranch: z.string(),
  commitMessages: z.array(z.string()),
  author: z.string(),
  timestamp: z.string(),
});

export const ScanConfigSchema = z.object({
  minimumSeverity: z.nativeEnum(SeverityLevel),
  useFilesystem: z.boolean(),
});

export const PullRequestContextSchema = z.object({
  owner: z.string(),
  repo: z.string(),
  number: z.number(),
  sha: z.string(),
});

export const ScanRequestSchema = z.object({
  files: z.array(FileRecordSchema).min(1, 'Files array cannot be empty'),
  metadata: GitMetadataSchema,
  config: ScanConfigSchema,
  sessionId: z.string().optional(),
  pullRequest: PullRequestContextSchema.optional(),
});

export const CommentSchema = z.object({
  file: z.string().nullable(),
  line: z.number().nullable(),
  body: z.string(),
});

export const PhaseResultsSchema = z.object({
  inventory: z.string(),
  tracing: z.string(),
  analysis: z.string(),
  filtering: z.string(),
  fixes: z.string(),
  comments: z.string(),
});

export const ScanResponseSchema = z.object({
  success: z.boolean(),
  phases: PhaseResultsSchema,
  comments: z.array(CommentSchema),
  commentsPosted: z.boolean().optional(), // True if server posted comments, false if action should post them
  batchCount: z.number().optional(),
  error: z.string().optional(),
});

// ============================================================================
// Installation Schemas (Database records)
// ============================================================================

export const SetupPrResultsSchema = z.object({
  created: z.array(
    z.object({
      repo: z.string(),
      prUrl: z.string(),
      prNumber: z.number(),
    }),
  ),
  failed: z.array(
    z.object({
      repo: z.string(),
      error: z.string(),
    }),
  ),
  skipped: z.array(z.string()),
});

export const CodeScanInstallationSchema = z.object({
  id: z.string(),
  installationId: z.number(),
  githubOrgId: z.number(),
  githubOrgName: z.string(),
  repositories: z.array(z.string()),
  deploymentType: z.nativeEnum(DeploymentType),
  serverUrl: z.string().nullable(),
  setupType: z.nativeEnum(SetupType).nullable(),
  status: z.nativeEnum(InstallationStatus),
  organizationId: z.string().nullable(),
  installerGithubUsername: z.string().nullable(),
  installerGithubId: z.number().nullable(),
  confirmedByEmail: z.string().nullable(),
  confirmedByUserId: z.string().nullable(),
  setupPrsInProgress: z.boolean(),
  setupPrsCreated: z.boolean(),
  setupPrResults: SetupPrResultsSchema.nullable(),
  installedAt: z.date(),
  confirmedAt: z.date().nullable(),
  deletedAt: z.date().nullable(),
  createdAt: z.date(),
  updatedAt: z.date(),
});

export const CodeScanSchema = z.object({
  id: z.string(),
  codeScanInstallationId: z.string().nullable(),
  organizationId: z.string(),
  userId: z.string().nullable(),
  repositoryFullName: z.string(),
  commitSha: z.string(),
  pullRequestNumber: z.number().nullable(),
  status: z.nativeEnum(CodeScanStatus),
  triggeredBy: z.nativeEnum(TriggeredBy),
  issuesFoundCount: z.number().nullable(),
  resultsSummary: z.any(), // JSONB field - flexible structure
  errorMessage: z.string().nullable(),
  startedAt: z.date(),
  completedAt: z.date().nullable(),
  durationMs: z.number().nullable(),
  createdAt: z.date(),
});

// ============================================================================
// API Request Schemas
// ============================================================================

export const ConfirmInstallationRequestSchema = z.object({
  installationId: z.number(),
  organizationId: z.string(),
  email: z.string().email(),
});

export const OnPremSetupRequestSchema = z.object({
  installationId: z.number(),
  serverUrl: z.string().url(),
  setupType: z.nativeEnum(SetupType),
});

export const CreateSetupPRsRequestSchema = z.object({
  installationId: z.number(),
  repositories: z.array(z.string()),
});

export const SetInstallationEmailRequestSchema = z.object({
  installationId: z.number(),
  email: z.string().email(),
});

// ============================================================================
// API Response Schemas
// ============================================================================

export const CodeScanInstallationResponseSchema = CodeScanInstallationSchema.extend({
  installedAt: z.string(),
  confirmedAt: z.string().nullable(),
  deletedAt: z.string().nullable(),
  createdAt: z.string(),
  updatedAt: z.string(),
  // Optional redirect flag for existing users
  shouldRedirectToLogin: z.boolean().optional(),
  message: z.string().optional(),
}).omit({
  setupPrResults: true,
}).extend({
  setupPrResults: SetupPrResultsSchema.nullable(),
});

export const CodeScanResponseSchema = CodeScanSchema.extend({
  startedAt: z.string(),
  completedAt: z.string().nullable(),
  createdAt: z.string(),
});

export const CodeScanListResponseSchema = z.object({
  scans: z.array(CodeScanResponseSchema),
  total: z.number(),
});

// ============================================================================
// GitHub Webhook Schemas
// ============================================================================

export const GitHubInstallationWebhookSchema = z.object({
  action: z.enum(['created', 'deleted', 'suspend', 'unsuspend', 'new_permissions_accepted']),
  installation: z.object({
    id: z.number(),
    account: z.object({
      login: z.string(),
      id: z.number(),
      type: z.string(),
    }),
    repository_selection: z.string().optional(),
    created_at: z.string().optional(),
    updated_at: z.string().optional(),
  }),
  repositories: z
    .array(
      z.object({
        id: z.number(),
        name: z.string(),
        full_name: z.string(),
      }),
    )
    .optional(),
  sender: z.object({
    login: z.string(),
    id: z.number(),
  }),
});

export const GitHubInstallationRepositoriesWebhookSchema = z.object({
  action: z.enum(['added', 'removed']),
  installation: z.object({
    id: z.number(),
    account: z.object({
      login: z.string(),
      id: z.number(),
    }),
  }),
  repositories_added: z
    .array(
      z.object({
        id: z.number(),
        name: z.string(),
        full_name: z.string(),
      }),
    )
    .optional(),
  repositories_removed: z
    .array(
      z.object({
        id: z.number(),
        name: z.string(),
        full_name: z.string(),
      }),
    )
    .optional(),
  sender: z.object({
    login: z.string(),
    id: z.number(),
  }),
});

// ============================================================================
// TypeScript Types (inferred from schemas)
// ============================================================================

// Scan Request/Response
export type FileRecord = z.infer<typeof FileRecordSchema>;
export type GitMetadata = z.infer<typeof GitMetadataSchema>;
export type ScanConfig = z.infer<typeof ScanConfigSchema>;
export type PullRequestContext = z.infer<typeof PullRequestContextSchema>;
export type ScanRequest = z.infer<typeof ScanRequestSchema>;
export type Comment = z.infer<typeof CommentSchema>;
export type PhaseResults = z.infer<typeof PhaseResultsSchema>;
export type ScanResponse = z.infer<typeof ScanResponseSchema>;

// Database records
export type SetupPrResults = z.infer<typeof SetupPrResultsSchema>;
export type CodeScanInstallation = z.infer<typeof CodeScanInstallationSchema>;
export type CodeScan = z.infer<typeof CodeScanSchema>;

// API Requests
export type ConfirmInstallationRequest = z.infer<typeof ConfirmInstallationRequestSchema>;
export type OnPremSetupRequest = z.infer<typeof OnPremSetupRequestSchema>;
export type CreateSetupPRsRequest = z.infer<typeof CreateSetupPRsRequestSchema>;
export type SetInstallationEmailRequest = z.infer<typeof SetInstallationEmailRequestSchema>;

// API Responses
export type CodeScanInstallationResponseDTO = z.infer<typeof CodeScanInstallationResponseSchema>;
export type CodeScanResponseDTO = z.infer<typeof CodeScanResponseSchema>;
export type CodeScanListResponseDTO = z.infer<typeof CodeScanListResponseSchema>;

// GitHub Webhooks
export type GitHubInstallationWebhook = z.infer<typeof GitHubInstallationWebhookSchema>;
export type GitHubInstallationRepositoriesWebhook = z.infer<
  typeof GitHubInstallationRepositoriesWebhookSchema
>;
